.App {
  text-align: center;
}

.App-header {
  padding-top: 2rem;
  background-color: #282c34;
  color: hsla(0, 0%, 100%, 0.856);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(20px + 2vmin);
  font-family: 'Qahiri', sans-serif;
  background: url("./images/k1.png") no-repeat opacify($color: #282c34, $amount: .3);
  background-position: center;
  background-size: 40%;
}
